body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.wrap {
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
}
.nav-style {
    width: 100%;
    margin-top: 20px;
    height: 50px;
    margin-bottom: 80px;
}

.nav1 {
    text-align: center;
    font-family: 'MedievalSharp', cursive;
    color: blue;
}

.nav2 {
    text-align: center;
    font-size: 14px;
    margin-top: 5px;
    color: white;
}
.card {
    height: 120px;
    margin: 10px;
    width: 120px;
    text-align: center;
    position: relative;
    
}

.card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
}

.card > .img-container > img {
    width: 100%;
}

.clicked {
    display: inline-block;
}
